@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&family=Montserrat&display=swap);
* {
	padding: 0px;
	margin: 0px;
	box-sizing: border-box;
}

body {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	font-family: "Montserrat", sans-serif;
}

header {
	min-height: 10vh;
}

main {
	min-height: 80vh;
}

footer {
	background-color: #8f8a8a;
	color: white;
	min-height: 10vh;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	position: relative;
	width: 100%;
}

.recipesList {
	width: 100vw;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
	padding: 1rem;
}

.recipeCard {
	margin: 1rem;
}

.cardImage {
	height: 200px;
	object-fit: cover;
}

.singleRecipeImage {
	object-fit: cover;
	width: 100vw;
	max-height: 100vh;
}

.searchBox {
	margin: 1rem;
	margin-left: 2rem;
}

.recipeInfo {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: flex-start;
	        justify-content: flex-start;
	-webkit-align-items: flex-start;
	        align-items: flex-start;
}

.recipeName {
	font-family: "Dancing Script", cursive;
	font-weight: 700;
	font-size: 10rem;
	margin: 1.5rem 0;
}

h3.recipeIngredients {
	font-family: "Dancing Script", cursive;
	font-size: 3rem;
	margin: 1rem 0;
}

.recipeInstructions {
	max-width: 60vw;
}

.recipeFluidContainer {
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.returnButton {
	-webkit-align-self: flex-start;
	        align-self: flex-start;
	margin: 1rem 0;
}

.recipeSpinner {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 3rem;
	height: 3rem;
}

#recipeForm {
	margin: 2rem;
}

.formInput {
	margin-top: 1rem;
}

#difficulty {
	background-color: white;
}

#addMoreButton {
	margin-top: 1rem;
}

#submitButton {
	margin-top: 1rem;
	width: 100%;
}

